import React from 'react';

const NavButton = (props) => {
  const icon = (props.prev) ? 'fa fa-chevron-left' : 'fa fa-chevron-right';
  return (
    <i className={icon}></i>
  );
};

NavButton.propTypes = {
  prev: React.PropTypes.bool,
};

export default NavButton;



